ul,li{
    list-style: none;
    padding: 0;
    margin: 0;
}

.line{
    width: 365px;
    height: 270px;
    /* border: solid 2px red; */
    margin-top:20px;
}
.nav{
    width: 170px;
    height: 260px;
    float: left;
    border-radius: 10px;
    margin-left: 10px;
    background-color: white;
    margin-bottom: 15px;
    text-align: center;
    color: black
}

.item{
    width: 170px;
    height: 260px;
    float: left;
    border-radius: 10px;
    background-color: white;
    text-align: center;
}
.item p{
    margin-top: 5px;
    line-height: 25px;
    margin-left: 5px;
    width: 160px;
}
.item>div{
    display: inline;
    margin-top: 20px;
}    
    
.item>div p{
    width: 50px;
    color: #fa3c0c;
    text-align: left;
    display: inline;
    line-height: 30px;
}
.item>div div{
    width: 80px;
    color: rgb(150, 150, 150);
    display: inline;
    text-align: left;
    margin-left: 20px;
    line-height: 30px;
}
.item img{
    width: 170px;
    height: 170px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}
