.body{
    background-color: rgb(226, 226, 226);
    width: 100%;
    height: 667px;
}

/*返回按钮*/
.icon-fanhui{
    height: 30px;
    width: 30px;
    border-radius: 15px;
    background-color: rgb(97, 97, 97, 0.5);
    color: white;
    position: fixed;
    top: 20px;
    left: 20px;
    text-align: center;
    line-height: 30px;
}

/*图片*/
.img{
    height: 375px;
    width: 375px;
}



/*底部按钮*/
.bott{
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 50px;
    border-top: solid 1px gray;
    background-color: white;
}
.but{
    height: 20px;
    width: 50px;
    float: left;
    text-align: center;
    line-height: 20px;
    color: grey;
    font-size: 13px;
    margin-top: 5px;
}
.active{
    color: red;
    /* background-color: red; */
}
.but p{
    margin-top: 0px;
}
.but i{
    height: 15px;
    width: 15px;
}
.but_1{
    color:orangered
}
/*购物车和购买*/
.tbut{
    width: 220px;
    height: 40px;
    text-align: center;
    float: left;
    color: white;
    font-weight: bolder;
    margin-top: 5px;
}
.but_4{
    height: 40px;
    width: 110px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    border: none;
    background-color: rgb(255, 180, 19);
    color: white;
    font-weight: bolder;
}
.but_5{
    height: 40px;
    width: 110px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    border: none;
    background-color: rgb(255, 94, 19);
    color: white;
    font-weight: bolder;
}